.backdrop {
  width: 100%;
  height: 100%;
  visibility: visible;
  z-index: 90;
  background-color: rgba(0, 0, 0, .2);
  padding: 94px 15px;
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

@media screen and (min-width: 768px) {
  .backdrop.is-hidden .backdrop.is-hidden .modal {
    transform: translate(-50%, -50%)scale(1.1);
  }
}

